
import { defineComponent,onMounted,reactive,toRefs } from 'vue'
import { useRouter } from 'vue-router'
import {getJson} from "@/api/apiConfig";
import { personalized } from "@/api/api"
import LoadingCpn from "@/components/common/loadingcpn.vue"
import Titlemm from "@/components/common/titlemm.vue"
import {myDate,goPage} from "@/utils/common"
interface state{
  SongSheetArr:[];
  today:string;
  dailytips:boolean;
}
export default defineComponent({
  	name:'Index',
  	components:{
		LoadingCpn,
        Titlemm
  	},
  	setup(){
        let state:state = reactive({
            SongSheetArr:[],
            today:'',
            dailytips:false,
        })
        onMounted(() => {
           getSongSheet() 
           state.today = myDate()
        })
        let timer:any = ''
        const debounce = (func:Function, delay = 300, thisArg?:any) => {
            return (...args:any) => {
                clearTimeout(timer)
                timer = setTimeout(func.bind(thisArg), delay, ...args)
            }
        }
        let leave = () => {
            if(timer){
                clearTimeout(timer)
            }
            state.dailytips = false
        }
        let enter = debounce(function(){
            state.dailytips = true
        },700)
        let getSongSheet = () => {
            getJson(personalized,{limit:9},(res:any) => {
                if(res.code == 200){
                    state.SongSheetArr = res.result
                }
            },(err:any) => {
                
            })
        }
        let router = useRouter();
        let goeverysongrmd = () => {
            router.push({
                path: '/everydaysongrmd'
            })
        }
		return{
			...toRefs(state),
            enter,
            leave,
            goeverysongrmd,
            goPage,
            router
		} 
  	}
})
